html {
  font-size: 20px;
}

body {
  margin: 0;
  font-family: "SB Sans Display";
  font-weight: normal;
  background: #040341;
  color: white;
  position: relative;
  touch-action: none;
  overscroll-behavior-y: none;
  overscroll-behavior: none;
}

@media (min-width: 2100px) {
  html {
    font-size: 24px;
  }
}

@media (max-width: 1400px) {
  html {
    font-size: 16px;
  }
}

@media (max-width: 1200px) {
  html {
    font-size: 14px;
  }
}

@media (max-width: 576px) {
  html {
    font-size: 10px;
  }
}
